$primary-color : #cc0000;
$background-color: #212121;
$heading-white: #B0B0B0;

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body{
    background-color: $background-color;
    color: #ffffff;
    user-select: none;
    font-family:  'sans-serif', 'Roboto';
    scroll-behavior: smooth !important;
}

/////////////////////////////////////    Navbar Start     ///////////////////////////////////

.brand{
    padding: 0;
    .navbar{
        background-color: #333333;
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 1;

        .navbar-icon{
            color: $heading-white !important;
            font-weight: bold;
            font-size: 25px;
            margin: 6px 0;
            &:hover{
                color: #ffffff;
            }
        }
    }
}

.MuiSvgIcon-root{
    background-color: #333333;
    color: #ffffff;
}

.MuiDrawer-paper{
    background-color: $heading-white !important;

    .menu-title{
        background-color: $background-color;
        color: $primary-color;
        padding: 20px 120px 0 16px;
        font-weight: 600;
        font-size: 25px;
        font-family: sans-serif;
        text-shadow: 1px 1px 1px $heading-white;
        margin: 0;
    }
    .menu-subtitle{
        background-color: $background-color;
        color: $heading-white;
        padding: 0 0 20px 16px;
        font-weight: 600;
        font-size: 10px;
        font-style: italic;
        font-family: sans-serif;
        border-bottom: 0.5px solid $heading-white;
        text-shadow: 1px 1px 1px $primary-color;
    }
    .menu-divider{
        background-color: $heading-white;
    }
    
    .MuiList-padding, .MuiListItem-gutters, .MuiListItem-root{
        padding: 0 !important;
    }
    
    .MuiTypography-root{
        font-family: sans-serif;
        padding: 8px 16px;
        color: $background-color;
        text-transform: uppercase;
        text-shadow: 1px 1px 3px black;
        &:hover{
            padding-left: 40px;
            border-left: 10px double $heading-white;
            border-right: 10px double $heading-white;
            background-color: $background-color;
            color: $heading-white;
        }
    }
}

/////////////////////////////////////Navbar End //////////////////////////////////

///////////////////////////////     Particle Js Start   ///////////////////////////////

.tsparticles-canvas-el{
    position: absolute;
    width: 100vw;
    height: 100vh;
}

///////////////////////////////     Particle Js End    ///////////////////////////////

/////////////////////////////////Home header start ///////////////////////////////

.tsparticles-canvas-el{
    position: absolute;
    width: 100vw;
    height: 100vh;
}

.header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),url("../src/image/header.jpg");
    background-color: rgba(248, 247, 216, 0.3); 
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    .header-title{
        color: $primary-color;
        font-weight: bold;
        font-family: sans-serif;
        text-transform: uppercase;
        text-shadow: 6px 4px 4px $background-color;
    }
    .header-subtitle{
        color: $background-color;
        font-weight: 600;
        font-family: sans-serif;
        font-size: 20px;
        text-transform: uppercase;
    }
    .header-btn{
        font-weight: 500;
        font-size: 16px;
        letter-spacing: 1px;
        display: inline-block;
        padding: 6px 30px;
        border-radius: 50px;
        transition: 0.5s;
        color: $background-color;
        border: 2px solid $primary-color;
        text-decoration: none;
        z-index: 10;
    }
    
    .header-btn:hover{
        background-color: $primary-color;
        color: white;
        text-decoration: none;
    }
}


///////////////////////////////////     About Page      /////////////////////////////////////

.page-title{
    border-left: 4px solid $primary-color;
    padding-left: 30px;
    text-transform: uppercase;
    font-family: sans-serif;
    font-weight: bold;
    text-shadow: 3px 3px 2px black;
}

.abt-heading{
    color: $primary-color;
    font-weight: bold;
    font-family: sans-serif;
    text-shadow: 3px 3px 2px black;
    
}

.abt-text{
    font-family: sans-serif;
    word-spacing: 3px
}

.abt-page{
    background-color: #333333;
    box-shadow: 0 0 20px 8px #333333;
    padding: 40px 30px;
    border-radius: 10px;
}

.header-btn{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 6px 30px;
    border-radius: 10px;
    transition: 0.5s;
    color: #ffffff;
    border: 2px solid $primary-color;
    text-decoration: none;
    z-index: 10;
}

.header-btn:hover{
    background-color: $primary-color;
    color: white;
    text-decoration: none;
}


///////////////////////////////////    End About Page      /////////////////////////////////////

///////////////////////////////////     Skills Page      ///////////////////////////////////////

.skill-page{
    @extend .abt-page;
}

.card{
    background-color: $background-color;
    box-shadow: 3px 3px 20px 2px black;
    &:hover{
        box-shadow: 3px 3px 20px 2px $heading-white;
    }
    .card-header{
        background-color: $primary-color;
        text-shadow: 2px 1px 1px black;
        font-size: bolder;
        font-family: sans-serif;
        font-size: 20px;
        text-transform: uppercase;
    }
    .card-text{
        font-family: sans-serif;
        padding: 20px 0;
        text-align: justify;
    }
}

///////////////////////////////////     End Skills Page      ///////////////////////////////////

///////////////////////////////////     Start Work Page      ///////////////////////////////////


.work-page{
    @extend .abt-page;
}

.work-img{
    border-radius: 20px;
    box-shadow: 3px 3px 20px 2px black;
    &:hover{
        box-shadow: 3px 3px 20px 2px $heading-white;
    }
}


///////////////////////////////////     End Work Page      ///////////////////////////////////

///////////////////////////////////     Start Contact Page      ///////////////////////////////////


.contact-page{
    @extend .abt-page;
    .label{
        font-weight: bolder;
        text-shadow: 2px 2px 1px black;
        font-size: 18px;
    }
    .form-control{
    box-shadow: 1px 1px 10px 1px black;
    &:hover{
        box-shadow: 3px 3px 20px 2px $heading-white;
    }
    }
    .detail-heading{
        color: $primary-color;
        padding: 10px 0;
        font-weight: bold;
        font-family: sans-serif;
        text-shadow: 2px 2px 1px black;
    }
    .contact-detail{
        font-family: sans-serif;
        text-shadow: 2px 2px 1px black;
    }
    .contact-detail-link{
        @extend .contact-detail;
        color: white;
        text-decoration: none;
        &:hover{
            margin-right: 20px;
        }
    }
}


///////////////////////////////////     End Contact Page      ///////////////////////////////////


/////////////////////////////////////    Footer Start     /////////////////////////////////////// 

.footer{
    text-align: center;
    background-color: black;
    color: white;
    padding: 20px;
    margin: 0;
    font-family: sans-serif;
}

/////////////////////////////////////    Footer End     ///////////////////////////////////////// 



/////////////////////////////////////    Media Query Start     ///////////////////////////////////////// 

@media screen and (max-width: 1440px) {
    .header-title{
        font-size: 90px;
    }
    .header-subtitle{
        font-size: 30px;
    }
}

@media screen and (max-width: 780px) {
    .header-title{
        font-size: 60px;
    }
    .header-subtitle{
        font-size: 15px;
    }
}


@media screen and (max-width: 380px) {
    .header-title{
        font-size: 45px;
    }
    .header-subtitle{
        font-size: 10px;
    }
}
  
/////////////////////////////////////    Media Query Start     ///////////////////////////////////////// 